        line-height: 15px;
        font-size: 1.2vw;
    }
    .product-container p, .resources-container p, .company-container p{
        margin-left: 35%;
        line-height: 15px;
        color: #b4b3b3;
        font-size: 1.6vw;
    }
    .fulmore-container h3{
        font-size: 3vw;
    }
    .fulmore-container p{
        font-size: 1.6vw;
    }
    .product-container h4, .resources-container h4, .company-container h4{
        font-size: 1.8vw;
    }
    .left-bottom-container{
        font-size: 1.6vw;
    }
    #footer .fulmore-container i{
        display: none;
    }
}
@media only screen and (max-width: 550px){
    .fulmore-container{
        display: none;
    }
    .product-container h4, .resources-container h4, .company-container h4{
        font-size: 2.3vw;
        margin-left: 5%;
    }
    .product-container p, .resources-container p, .company-container p{
        font-size: 2vw;
        margin-left: 5%;
    }
}


:root{
    --clr-primary-light: #EEEEEE;
    --clr-primary: #8c8c8c;
    --clr-accent: #929e89;
    --clr-accent-two: #a2c488;
    --clr-accent-three: #D1E2C4;

    --ff-primary: 'Alegreya', serif;
    --ff-paragraph: 'Montserrat', sans-serif;
}

.form-section{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    height: 100vh;
    font-family: 'Montserrat', sans-serif;
    font-family: var(--ff-paragraph);
}
.wrapper{
    position: relative;
    padding: 1em;
}
.form-container{
    background: white;
    width: min(90%, 30em);
    padding: 3em;
    border-radius: 1em;
}
.form-container::before{
    content: '';
    background: #929e89;
    background: var(--clr-accent);
    position: absolute;
    height: 570px;
    width: 70%;
    border-radius: 1em;
    right: 0;
    top: 0;
    z-index: -1;
}
form{
    display: -webkit-flex;
    display: flex;
    -webkit-flex-direction: column;
            flex-direction: column;
}
label{
    margin-top: 1em;
    margin-bottom: -.1em;
    font-weight: 600;
    font-size: .9em;
}
input{
    padding: .5em;
    border: solid #929e89 1px;
    border: solid var(--clr-accent) 1px;
    font-size: .9em;
}
input:focus{
    outline: solid #929e89 1px;
    outline: solid var(--clr-accent) 1px;
}
.submit{
    color: white;
    background: #929e89;
    background: var(--clr-accent);
    border: none;
    width: 100%;
    margin-top: 1em;
    margin-bottom: .3em;
    padding: .5em;
}
.return-btn{
    padding: .5em 2.7em;
    width: 100%;
    font-family: 'Montserrat', sans-serif;
    font-family: var(--ff-paragraph);
    font-size: .9em;
    color: white;
    background: #929e89;
    background: var(--clr-accent);
    border: none;
    outline: none;
}
@media(min-width: 420px){
    .form-container::before{
        height:520px;
        width: 67%;
        right: 5%;
    }
}
